input,
textarea,
select {
	margin-bottom: 1.5rem;
}

figure,
form {
	margin-bottom: 2.5rem;
}

input[type="email"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea,
select {
	padding: 9px 10px;
	background-color: #fff;
	border: 1px solid #D1D1D1;
	box-shadow: none;
	box-sizing: border-box;
}

input[type="email"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea {
	appearance: none;
}

textarea {
	min-height: 65px;
	padding-top: 6px;
	padding-bottom: 6px;
}

input[type="email"]:focus,
input[type="search"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
textarea:focus,
select:focus {
	border: 1px solid $primary-color;
	outline: 0;
}

label,
legend {
	display: block;
	margin-bottom: .5rem;
	font-weight: 600;
}

fieldset {
	padding: 0;
	border-width: 0;
	margin-bottom: 1.5rem;
}

input[type="checkbox"],
input[type="radio"] {
	display: inline;
	margin-right: .5em;
}

label > .label-body {
	display: inline-block;
	margin-left: .5rem;
	font-weight: normal;
}
